.body {
  font-family: "Montserrat", sans-serif;
}

label {
  font-family: "Montserrat", sans-serif;
}

div {
  font-family: "Montserrat", sans-serif;
}



.header-sc {
  background-color: #fca311;
  grid-row: 1;
  grid-column: 1 /-1;
}

.header-sc h1 {
  color: #fff;
  font-weight: 900;
  font-family: "Montserrat", sans-serif;
  margin: 20px;
}

.item1 { grid-area: header; }
.item2 { grid-area: main; }
.item4 { grid-area: footer; }

.grid-container-app {
  display: grid;
  grid-template-areas:
    'header header header'
    'main main main'
    'footer footer footer';
  grid-gap: 10px;
  padding: 10px;
}

.item5 { grid-area: left; }
.item6 { grid-area: right; }


.grid-container-main {
  display: grid;
  grid-template-areas:
    'left left left right';
  grid-gap: 5px;
  padding: 10px;
  margin: 10px 5px;
  height: 100%;
  width: 100%;
  font-size: 14px;
  text-align: left;
}


.calculate {
  border-radius: 4px;
  position: relative;
  padding: 0px 0px;
  background-color: white;
  margin: 5px;
  height: 80vh;
  width: 400px;
  box-shadow: 0 2px 5px #ccc;
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  text-align: center;
  justify-content: center;
 


}
.total-expenditure {
  margin: 20px 0px 0px 10px;
  background-color: #3b054f;
  height: 50%;
  width: 90%;
  border-radius: 4px;
  justify-content: center;
  text-align: left;
}
.summary-food {
  padding: 15px 0 0 0;
  margin: 10px 0 0 10px;
  font-size: 12x;
  color: white;
}

.title-food-expenditure {
  padding: 15px 0 0 0;
  margin: 10px 0 0 10px;
  font-size: 16px;
  color: white;
}

.total-food-expenditure {
  margin: 5px 0 0 10px;
  font-size: 30px;
  color: white;
  font-weight: 900;
}

.img {
  width: 90px;
  height: 90px;
  justify-content: center;
}
.img-expenditure {
  width: 120px;
  height: 120px;
  justify-content: center;
}

.income-group-grid{
  display: grid;
  grid-template-columns: 30% 70%;
}

.income-group {
  margin: 20px 0 0 0;
 
}

.income-level-group{
  font-size: 65px;
}

.total-expenditure-top {
  position: top;
  height: 8px;
  margin: 0 0 40px 0;

  background-color: #fca311;
}

.step-block{
  display: grid;
  grid-template-columns: auto auto auto auto;
  height: 60%;
}

.header-step{
font-weight: bold;
margin-bottom: 20px;

}

.step-item{
  margin: 20px;
  padding: 20px;
  font-size: 30px;
  text-align: center;
  grid-gap: 5px;
  border-radius: 4px;
  padding: 20px;
  background-color: #f8f9fa;
  margin: 30px 5px;
  height: auto;
  width: auto;
  box-shadow: 0 2px 5px #ccc;
  font-size: 14px;
  text-align: left;
}


.income-grid {
  display: grid;
  gap: 10px;
  margin: 20px;
  padding: 20px;
  font-size: 30px;
  text-align: center;
  border-radius: 4px;
  padding: 20px;
  background-color: #f8f9fa;
  margin: 30px 5px;
  height: 200px;
  width: auto;
  box-shadow: 0 2px 5px #ccc;
  font-size: 14px;
  text-align: left;



}

.text-input-income{
  margin-top:100px;
  
  }
  .card-income input {
    margin: 60px 50px;
    width: 100%;
    border: 2px solid #aaa;
    border-radius: 4px;
    margin: 8px 0;
    outline: none;
    padding: 8px;
    padding-top: 20px;
    box-sizing: border-box;
    transition: 0.3s;
    text-align: center;
    font-size: 20px;
  }


  .card-income input:focus {
    border-color: dodgerBlue;
    box-shadow: 0 0 8px 0 dodgerBlue;
  }
  
  .card-income p {
    font-size: 20px;
  }
  
  .button-next{
    width: 140px;
    height: 40px;
    border-radius: 4px;
    border: none;
    background: #3b054f;
    color: #fff;
    font-size: 16px;
    text-align: center;
    margin: 30px;
  
  }



  .tab_content{
    height: auto;
    width: auto;

  }

  .card-demogrphy {
    display: grid;
    gap: 4px;
  }

  .card {
    border-radius: 4px;
    position: relative;
    padding: 25px 15px;
    background-color: #f8f9fa;
    margin: 10px 20px;
    height: 120px;
    width: 70%;
    box-shadow: 0 2px 5px #ccc;
    font-family: "Montserrat", sans-serif;
    font-size: 14px;
    text-align: left;
  }

  .header-card-1{
    font-size: 17px;
    margin-bottom: 30px;
    font-weight: bold;
  
  
  }
  
  .card-2 label {
    margin: 20px 0 0 0;
  }
  
  .diff-card {
    margin: 20px 0 0 0;
  }
  
  .box {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: center;
  }
  
  .box .value.left {
    left: -22px;
  }
  .box .value.right {
    right: -43px;
  }
  
  .slider-container {
    position: relative;
    margin: 20px 0 0 0;
    height: 100%;
    width: 100%;
  }
  .slider-container .bar {
    position: absolute;
    z-index: 1;
    left: 2px;
    top: 7px;
    width: 100%;
    height: 10px;
    border-radius: 5px;
    background-color: #c6aee7;
    overflow: hidden;
  }
  .slider-container .bar .fill {
    display: block;
    width: 0;
    height: 100%;
    background-color: #6200ee;
  }
  .slider-container .slider {
    position: relative;
    z-index: 2;
    -webkit-appearance: none;
    width: 100%;
    height: 10px;
    border-radius: 5px;
    outline: none;
    background-color: transparent;
  }
  .slider-container .slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 25px;
    height: 25px;
    background-color: #6200ee;
    border-radius: 50%;
    cursor: pointer;
    outline: none;
    box-shadow: 0 0 0 0 rgba(98, 0, 238, 0.1);
    transition: 0.3s ease-in-out;
  }
  .slider-container .slider::-webkit-slider-thumb:hover {
    box-shadow: 0 0 0 20px rgba(98, 0, 238, 0.1);
  }
  .slider-container .slider:active::-webkit-slider-thumb {
    box-shadow: 0 0 0 40px rgba(98, 0, 238, 0.2);
  }

  .card-radiobtn-2 {
    height: 120px;
  }
  .card-radiobtn-3 {
    height: 160px;
  }
  
  .card-radiobtn-4 {
    height: 200px;
  }
  
  .card input {
    margin: 10px 0 0 0;
    border: 2px solid #aaa;
    border-radius: 4px;
    outline: none;
    padding: 4px;
    box-sizing: border-box;
    transition: 0.3s;
  }
  
  .food-policy-container {
    background-color: white;
    padding: 20px;
  
  }
  .scroll {
    width: 100%;
    height: 70vh;
    background: #e5e5e5;
    overflow: auto;
  }
  .scroll::-webkit-scrollbar {
    width: 12px;
  }
  
  .scroll::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    border-radius: 10px;
  }
  
  .scroll::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
  }
  
